.header-anon {
  width: 100%;
  height: 100px;
  background-color: #282c34;
  color: #f6f6f6;
  display: flex;
  align-items: center;
  justify-content: left;
}

.header-anon h3 {
  margin: 0;
  padding-left: 50px;
}

.upload-filed {
  position: relative;
  height: 100px;
}

p.file-name {
  position: absolute;
  left: 145px;
  top: 15px;
}

.json-uploaded-box p.explain {
  margin: 0;
  margin-bottom: 20px;
  text-transform: capitalize;
  font-weight: bold;
  padding-bottom: 13px;
  border-bottom: 1px solid #ccc;
}
.json-uploaded-box {
  width: 45%;
  float: left;
  margin-right: 1%;
  padding: 15px;
  box-shadow: 2px 2px 36px -8px #ccc;
  border-radius: 10px;
}

span.MuiTypography-root.MuiFormControlLabel-label.MuiTypography-body1 {
  font-size: 0.8em!important;
}
span.MuiButtonBase-root.MuiIconButton-root.PrivateSwitchBase-root-1.MuiCheckbox-root.MuiCheckbox-colorSecondary.MuiIconButton-colorSecondary {
  padding: 10px!important;
}

span.file-name-evidence {
  color: #f6f6f6;
  font-size: 1.3em;
  font-weight: 700;
  color: #84a59e;
}

.legend-box {
  clear: both;
  height: 70px;
  width: 98%;
  background: #f6f6f6;
  padding: 5px;
  border-radius: 5px;
  border: 1px dashed #ccc;
}
svg.MuiSvgIcon-root.upload-icon {
  position: relative;
  top: 4px;
}

p.legend {
  float: left;
  margin-right: 40px;
  font-size: 0.8em;
  margin: 0;
  justify-content: center;
  display: flex;
  align-items: center;
  position: relative;
  top: 20px;
  margin-right: 40px;
}
.legend .MuiSvgIcon-root {
  width: 0.8em;
  height: 1em;
  display: inline-block;
  font-size: 1.5rem;
  transition: fill 200ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
  flex-shrink: 0;
  user-select: none;
  fill: currentColor;
}

span.MuiButtonBase-root.MuiIconButton-root.PrivateSwitchBase-root-1.MuiCheckbox-root.MuiCheckbox-colorSecondary.encrypt-icon.PrivateSwitchBase-checked-2.Mui-checked.MuiIconButton-colorSecondary {
  color: green;
}
span.MuiButtonBase-root.MuiIconButton-root.PrivateSwitchBase-root-1.MuiCheckbox-root.MuiCheckbox-colorSecondary.delete-icon.PrivateSwitchBase-checked-2.Mui-checked.MuiIconButton-colorSecondary {
  color: red;
}

pre {
  white-space: pre-wrap;
  background: #f6f6f6;
  padding: 30px;
  border-radius: 5px;
}

.obj-control-box {
  clear: both;
  width: 100%;
  height: 50px;
  text-align: center;
  justify-content: center;
  display: flex;
  padding-bottom: 30px;
  border-bottom: 1px dashed #ccc;
}
h2.crash {
  position: absolute;
  top: 170px;
  text-transform: uppercase;
  color: #333;
  font-size: 2em;
}

.first-choice-box h3 {
  font-size: 3em;
  text-transform: uppercase;
}

.loading {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  background: rgba(255,255,255,0.9);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 999;
}

.first-choice-box {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  background: #f6f6f6;
  z-index: 999;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  background-repeat: no-repeat;
  background-size: cover;
}

img.logo {
  width: 100px;
}
.encrypt-btn {
  cursor: pointer;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: lightblue;
  border-radius: 10px;
  font-weight: 700;
  margin-top: 40px;
}


p.choice-option {
  width: 40%;
  margin-left: auto;
  margin-right: auto;
  height: 80px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: salmon;
  border-radius: 10px;
  border: 1px solid #ddd;
  cursor: pointer;
  text-transform: uppercase;
  font-weight: 700;
  color: #333;
}

p.choice-option:hover {
  opacity: 0.8;
}

.encrypt-btn {
  text-transform: capitalize;
}

.obj-control {
  cursor: pointer;
  float: left;
  margin: 0 30px 0 30px;
  background-color: #333;
  color: #fff;
  display: flex;
  align-items: center;
  align-items: center;
  padding: 20px;
  border-radius: 5px;
}